const CardLitRenderer = {};
const block0 = (context) => { return html`<div 	id="${ifTruthy(context.ctr._id)}"	data-sap-ui="${ifTruthy(context.ctr._id)}"	aria-hidden="${ifTruthy(context.ariaHidden)}" class="sapFCard"><header class="sapFCardHeader">		${ context.image ? block1(context) : undefined }${ context.icon ? block2(context) : undefined }<div class="sapFCardHeaderText">			${ context.ctr.heading ? block3(context) : undefined }${ context.ctr.subtitle ? block4(context) : undefined }</div><span class="sapFCardStatus">${ifTruthy(context.ctr.status)}</span></header><section class="sapFCardContent">		${ repeat(context.ctr.content, undefined, (item, index) => block5(item, index, context)) }</section></div>`; };
